@use "sass:math";
@use "sass:map";
@import "../PageLoadAnimation/style.module.scss";
@import "./style.module.scss";
$width: 49px;
$aspect-ratio: 1.69;
$height: math.div($width, math.sqrt(3));
$hex-position: (
  1: 1 * 100px,
  //301.5012970326px,
  2: 2 * 100px,
  //37.5628738078px,
  3: 3 * 100px,
  //-480.087932px,
  4: 4 * 100px,
  //391.707433386px,
  5: 5 * 100px,
  //-100.8815403307px,
  6: 6 * 100px,
  //70.8815403307px,
);
$time: (
  1: 100s,
  //301.5012970326px,
  2: 125s,
  //37.5628738078px,
  3: 60s,
  //-480.087932px,
  4: 90s,
  //391.707433386px,
  5: 200s,
  //-100.8815403307px,
  6: 300s,
  //70.8815403307px,
);
$background-hexagon-count: 8;

.home_background {
  position: absolute;
  top: 50%;
  left: 50%;
  //   width: 100%;
  //   height: 100%;
  border-radius: 50%;
  opacity: 0.7;
  z-index: 3;
}
.background_hexagon {
  position: absolute;
  width: $width;
  height: $height;
  color: yellowgreen;
  //   top: 0%;
  //   left: 0%;
  top: 0%;
  left: 0%;
  box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
  background-color: $hexColor;
  //   animation: rotate 120s infinite;
  //   animation-direction: reverse;
  transform-origin: center;
  z-index: 3;
  scale: 0;
  //   rotate: 60deg;
  // transform: scale(0);
}
.background_hexagon:before,
.background_hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 24px solid transparent;
  border-right: 25px solid transparent;
}
$bottomBorder: math.div($width, (math.sqrt(3)) * 2);
.background_hexagon:before {
  bottom: 100%;
  border-bottom: $bottomBorder solid $hexColor;
}

.background_hexagon:after {
  top: 100%;
  width: 0;
  border-top: $bottomBorder solid $hexColor;
}
$animation-values: 2s forwards 8s;

@each $n, $horizontalOffset in $hex-position {
  @keyframes translate-#{$n} {
    0% {
      left: 0;
    }

    100% {
      left: $horizontalOffset;
    }
  }
  .home_background:nth-child(#{$n}) .background_hexagon {
    //   top: -296.4309737846px;
    // left: $horizontalOffset;
    animation: scale $animation-values, translate-#{$n} 5s forwards 9s;
  }
}
@each $n, $t in $time {
  .home_background:nth-child(#{$n}) {
    animation: rotate $t infinite;
  }
}
